html {
  width: 100%;
  height: 100%;  
  overflow: hidden;
  padding: 0;
  margin: 0;
  outline: 0;
}

body {
  width: 100%;
  height: 100%;
  padding: 0;
  margin: 0;
  overflow: hidden;
  background: transparent;
  font-family: 'Roboto', sans-serif;
  -webkit-user-select: none;
  user-select: none;
  -webkit-tap-highlight-color: rgba(255, 255, 255, 0);
  color: #FFFFFF; /* #222; /* #FEFEFE; */
}

/* ****************************************************************************** */
/* typography */

h1, h2, h3, h4, h5, h6 {
  margin: 0px 0px 1rem 0px;
}

p {
  margin: 0px 0px 1em 0px;
}


/* ****************************************************************************** */
/* controls */
.controlsWrapper {
  position: absolute;
  width: 300px; /* 100%; */
  height: 70px; /* 35px; /* 70px; */
  bottom: 0px; /* 1.75rem; /* 3%; */
  left: 0px;
  background: #222;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
}

.btn {
  /* position: absolute; */
  cursor: pointer;
  opacity: 0.9;
  -webkit-user-select: none;
  user-select: none;
  text-indent: -999em;
  /* border: 1px solid red; */
  width: 50px;
  height: 50px;
  background-position: center;
  background-repeat: no-repeat;
  background-size: 50%; /* contain; */
}
.btn:hover {
  opacity: 1;
}

#playBtn {
  background-image: url('data:image/jpeg;base64,');
  margin: 0px;
}
#pauseBtn {
  background-image: url('data:image/jpeg;base64,');
  display: none;
}
#prevBtn {
  background-image: url('data:image/jpeg;base64,');
  margin: 0px;
}
#nextBtn {
  background-image: url('data:image/jpeg;base64,');
  margin: 0px;
}
#playlistBtn {
  background-image: url('data:image/jpeg;base64,');
  margin: 0px;
}
#volumeBtn {
  background-image: url('data:image/jpeg;base64,');
  margin: 0px;
}

/* ****************************************************************************** */
/* peakFileWrapper */

.durationWrapper {
  position: absolute;
  width: 200px; /* 100%; */
  height: 70px;
  bottom: 0px;
  left: 300px;
  background: rgba(0,0,0,0.4); /* rgba(17, 10, 20, 0.65); */
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  /* text-align: left; */
  font-size: 26px;
  font-weight: 300;  
}



/* ****************************************************************************** */
/* peakFileWrapper */
.peakFileWrapper {
  position: absolute;
  width: calc(100vw - 500px); /* 100%; */
  height: 70px;
  bottom: 0px;
  left: 500px;
  background: #444;
}


/* Fade-In */
.fadeout {
  -webkit-animation: fadeout 0.5s;
  -ms-animation: fadeout 0.5s;
  animation: fadeout 0.5s;
}
.fadein {
  -webkit-animation: fadein 0.5s;
  -ms-animation: fadein 0.5s;
  animation: fadein 0.5s;
}
@keyframes fadein {
  from { opacity: 0; }
  to   { opacity: 1; }
}
@-webkit-keyframes fadein {
  from { opacity: 0; }
  to   { opacity: 1; }
}
@-ms-keyframes fadein {
  from { opacity: 0; }
  to   { opacity: 1; }
}
@keyframes fadeout {
  from { opacity: 1; }
  to   { opacity: 0; }
}
@-webkit-keyframes fadeout {
  from { opacity: 1; }
  to   { opacity: 0; }
}
@-ms-keyframes fadeout {
  from { opacity: 1; }
  to   { opacity: 0; }
}




